import React, { useCallback, useState, useRef, useEffect } from 'react';
import { Stack, Box, CircularProgress } from '@mui/material';
import { PlayArrow } from '@mui/icons-material';
import YouTube from 'react-youtube';
import { useSwipeable } from 'react-swipeable';

const videoData = [
    { id: 1, videoId: 'T2Oaj_CgUJo' },
    { id: 2, videoId: 'GEWdczVfkZI' },
    { id: 3, videoId: 'elpC1iEJ4i4' }
];

const VideoPlayer = ({ videoId, onReady, onEnd, onError }) => {
    const opts = {
        height: '100%',
        width: '100%',
        playerVars: {
            autoplay: 1,
            controls: 0,
            modestbranding: 1,
            rel: 0,
            showinfo: 0
        },
    };

    return (
        <YouTube
            style={{ height: '100vh' }}
            videoId={videoId}
            opts={opts}
            onReady={onReady}
            onEnd={onEnd}
            onError={onError}
        />
    );
};

function App() {
    const [currentIndex, setCurrentIndex] = useState(null);
    const [loading, setLoading] = useState(false);
    const [isPlaying, setIsPlaying] = useState(true);
    const playerRef = useRef(null);

    useEffect(() => {
        if (playerRef?.current) {
            if (isPlaying) {
                playerRef.current.playVideo();
            } else {
                playerRef.current.pauseVideo();
            }
        }
    }, [isPlaying]);

    const handlers = useSwipeable({
        onSwipedUp: () => changeVideo('next'),
        onSwipedDown: () => changeVideo('prev'),
        swipeDuration: 500,
        preventDefaultTouchmoveEvent: true,
        trackMouse: true
    });

    const changeVideo = (direction) => {
        if (currentIndex !== null) {
            setLoading(true);
            let newIndex;
            if (direction === 'next') {
                newIndex = (currentIndex + 1) % videoData.length;
            } else {
                newIndex = (currentIndex - 1 + videoData.length) % videoData.length;
            }
            setCurrentIndex(newIndex);
        }
    };

    const handleVideoReady = (event) => {
        setLoading(false);
        playerRef.current = event.target;
        event.target.playVideo();
    };

    const handleClick = () => {
        setIsPlaying((prev) => !prev);
    };

    const VideoView = useCallback(() => {
        return (
            <Box
                sx={{
                    background: '#333',
                    height: '100vh',
                    width: '100vw',
                    display: 'flex',
                    justifyContent: 'center',
                    position: 'relative',
                    pointerEvents: 'none'
                }}
            >
                <Box
                    sx={{
                        position: 'absolute',
                        height: '100%',
                        width: '100%',
                        display: 'flex',
                        justifyContent: 'center',
                        zIndex: 9999
                    }} />
                <Box
                    sx={{
                        position: 'absolute',
                        top: 0,
                        left: 0,
                        height: '100%',
                        width: '100%',
                        pointerEvents: 'auto'
                    }}
                    {...handlers}
                    onClick={handleClick}  // Toggle play/pause on click
                >
                    <Box sx={styles.videoWrapper}>
                        <Box sx={{
                            position: 'absolute',
                            height: '100%',
                            width: '100%',
                            background: loading ? 'black' : 'transparent',
                            zIndex: 1,
                            color: 'black',
                            fontSize: '1.5rem'
                        }}>
                            {loading && <CircularProgress />}
                        </Box>
                        <Box sx={{
                            position: 'absolute',
                            bottom: 0,
                            height: '20vh',
                            width: '100%',
                            zIndex: 1,
                            background: 'linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0))'
                        }} />
                        {currentIndex !== null && (
                            <VideoPlayer
                                videoId={videoData[currentIndex].videoId}
                                onReady={handleVideoReady}
                                onEnd={() => changeVideo('next')}
                                onError={() => console.error('Video failed to load')}
                            />
                        )}
                    </Box>
                </Box>
            </Box>
        );
    }, [currentIndex, loading, handlers]);

    return (
        <div style={styles.container}>
            {currentIndex !== null ? (
                <VideoView />
            ) : (
                <Stack direction="row" spacing={2} style={styles.thumbnailContainer}>
                    {videoData.map((item, index) => (
                        <div key={index} style={styles.thumbnail}>
                            <Box
                                onClick={() => setCurrentIndex(index)}
                                style={{
                                    cursor: 'pointer',
                                    position: 'absolute',
                                    zIndex: 9999,
                                    top: '10px',
                                    left: 0,
                                    width: '100%',
                                    height: '100%',
                                    display: 'flex',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                    background: 'transparent'
                                }}
                            >
                                <Box
                                    sx={{
                                        background: '#000',
                                        width: '30%',
                                        height: '30%',
                                        borderRadius: '50%',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                    }}
                                >
                                    <PlayArrow sx={{ color: 'white', fontSize: '2rem' }} />
                                </Box>
                            </Box>

                            <div style={styles.iframeContainer}>
                                <iframe
                                    src={`https://www.youtube.com/embed/${item.videoId}?enablejsapi=1&controls=0&loop=1&rel=0&allowfullscreen=0&showinfo=0&autohide=1`}
                                    style={styles.iframe}
                                    title="YouTube video player"
                                    frameBorder="0"
                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                    allowFullScreen
                                />
                            </div>
                        </div>
                    ))}
                </Stack>
            )}
        </div>
    );
}

const styles = {
    container: {
        display: 'flex',
        alignItems: 'center',
        height: '100vh',
        position: 'relative',
        overflow: 'auto'
    },
    videoWrapper: {
        width: '100%',
        height: '100%',
        position: 'relative',
        zIndex: 9999
    },
    thumbnailContainer: {
    },
    thumbnail: {
        width: '315px',
        height: '500px',
        position: 'relative',
        aspectRatio: '315 / 500'
    },
    iframeContainer: {
        position: 'relative',
        width: '100%',
        height: '100%',
        overflow: 'hidden'
    },
    iframe: {
        border: 'none',
        width: '100%',
        height: '100%',
        cursor: 'pointer'
    }
};

export default App;
