import React, { useCallback, useEffect, useRef, useState } from 'react';
import { Stack, Box, CircularProgress } from '@mui/material';
import { PlayArrow } from '@mui/icons-material';
import videojs from 'video.js';
import 'video.js/dist/video-js.css';
import 'videojs-youtube';
import { useSwipeable } from 'react-swipeable';

const videoData = [
  { id: 1, src: 'https://www.youtube.com/embed/T2Oaj_CgUJo' },
  { id: 2, src: 'https://www.youtube.com/embed/GEWdczVfkZI' },
  { id: 3, src: 'https://www.youtube.com/embed/elpC1iEJ4i4' }
];

function App() {
  const [currentIndex, setCurrentIndex] = useState(null);
  const [loading, setLoading] = useState(true);
  const videoRef = useRef(null);
  const playerRef = useRef(null);

  useEffect(() => {
    if (currentIndex !== null) {
      const currentSrc = videoData[currentIndex].src;
      if (videoRef.current) {
        playerRef.current = videojs(videoRef.current, {
          techOrder: ['youtube'],
          autoplay: true,
          controls: false,
          responsive: true,
          sources: [{ src: `${currentSrc}?modestbranding=1&;showinfo=0&;autohide=1&;rel=0;`, type: 'video/youtube' }]
        });

        playerRef.current.on('pause', () => {
          console.log('Video paused');
        });

        playerRef.current.on('loadedmetadata', () => {
          console.log('Video is loaded');
        });

        // Handle video end
        playerRef.current.on('ended', () => {
          changeVideo('next')
        });

        return () => {
          if (playerRef.current) {
            playerRef.current.dispose();
          }
        };
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentIndex]);

  const handlers = useSwipeable({
    onSwipedUp: () => changeVideo('next'),
    onSwipedDown: () => changeVideo('prev'),
    swipeDuration: 500,
    preventDefaultTouchmoveEvent: true,
    trackMouse: true
  });

  const changeVideo = (direction) => {
    if (currentIndex !== null) {
      setLoading(true);
      let newIndex;
      if (direction === 'next') {
        newIndex = (currentIndex + 1) % videoData.length;
      } else {
        newIndex = (currentIndex - 1 + videoData.length) % videoData.length;
      }
      setCurrentIndex(newIndex);
    }
  };

  // Play/Pause functionality when clicking on the video
  const togglePlayPause = () => {
    if (playerRef.current) {
      if (playerRef.current.paused()) {
        playerRef.current.play();
      } else {
        playerRef.current.pause();
      }
    }
  };

  const VideoView = useCallback(() => {
    return (
      <Box
        sx={{
          background: '#333',
          height: '100vh',
          width: '100vw',
          display: 'flex',
          justifyContent: 'center',
          position: 'relative',
          pointerEvents: 'none'
        }}
      >
        <Box
          sx={{
            position: 'absolute',
            height: '100vh',
            width: '100vw',
            display: 'flex',
            justifyContent: 'center',
            zIndex: 9999
          }} /><Box
          sx={{
            position: 'absolute',
            top: 0,
            left: 0,
            height: '100%',
            width: '100%',
            pointerEvents: 'auto'
          }}
          {...handlers}
          onClick={togglePlayPause} /><Box style={styles.videoWrapper}>
          <div data-vjs-player style={styles.videoWrapper}>
            <Box sx={{
              position: 'absolute',
              height: '10vh',
              width: '500px',
              background: '#fff',
              zndex: 1,
              color: 'black'
            }}>
              Ad
            </Box>
            <Box sx={{
              position: 'absolute',
              bottom: 0,
              height: '20vh',
              width: '500px',
              zndex: 1,
              // background: 'linear-gradient(to top, rgb(253, 2, 2), rgba(252, 5, 5, 0))'
              background: 'linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.9),  rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0))'
            }} />
            <video ref={videoRef} className="video-js vjs-default-skin" controls style={styles.videoWrapper} />
          </div>
        </Box>
      </Box>
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentIndex]);

  return (
    <div style={styles.container}>
      {currentIndex !== null ? (
        <VideoView />
      ) : (
        <Stack direction="row" spacing={2} style={styles.thumbnailContainer} overflow={"auto"}>
          {videoData.map((item, index) => (
            <div key={index} style={{ width: "315px", height: "500px", position: "relative" }}>
              <Box
                onClick={() => setCurrentIndex(index)}
                style={{
                  cursor: 'pointer',
                  position: 'absolute',
                  zIndex: 9999,
                  top: '10px',
                  left: 0,
                  width: "100%",
                  height: "100%",
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  background: 'transparent'
                }}
              >
                <Box
                  sx={{
                    background: '#000',
                    width: '120px',
                    height: '120px',
                    borderRadius: '30px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                  }}
                >
                  <PlayArrow sx={{ color: 'white', fontSize: '3.5rem' }} />
                </Box>
              </Box>

              <div style={{ position: 'relative' }}>
                <iframe
                  src={`${item.src}?enablejsapi=1&controls=0&loop=1&rel=0&allowfullscreen=0&showinfo=0&autohide=1`}
                  width="315"
                  height="500"
                  title="YouTube video player"
                  frameBorder="0"
                  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                  allowFullScreen
                />
              </div>
            </div>
          ))}
        </Stack>
      )}
    </div>
  );
}

const styles = {
  container: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    height: '100vh',
    position: 'relative',
    overflow: 'hidden'
  },
  videoWrapper: {
    width: '500px',
    height: '100%',
    position: 'relative',
    zIndex: 9999
  },
  thumbnail: {
    cursor: 'pointer',
    position: 'relative'
  },
  iframe: {
    border: 'none',
    cursor: 'pointer'
  }
};

export default App;
